.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin: 3rem;
  }
  
  .container > * {
    min-width: 25rem;
    max-width: 40rem;
  }
  
  .image {
    border-radius: 3rem;
    flex: 1 0;
    object-fit: cover;
    height: 25rem;
    margin: 1rem;
  }
  
  .details {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex: 1 0;
    border-radius: 3rem;
    padding: 2rem;
    color: black;
    margin-left: 1rem;
  }
  
  .header {
    display: flex;
    justify-content: space-between;
  }
  
  .name {
    font-size: 2rem;
    font-weight: bold;
  }
  
  .favorite {
    color: #e72929;
    font-size: 2.5rem;
  }
  
  .favorite.not {
    color: grey;
  }
  
  .cook_time {
    margin-top: 1rem;
  }
  
  .cook_time span {
    padding: 0.6rem 2rem 0.6rem 0;
    border-radius: 10rem;
    font-size: 1.3rem;
  }
  
  .price {
    font-size: 1.8rem;
    margin: 2rem 2rem 2rem 0;
    color: rgb(42, 88, 255);
  }
  
  .price::before {
    content: 'Price: ';
    color: darkgrey;
  }
  
  .container button {
    color: white;
    background-color:hsl(42, 100%, 57%);
    border: none;
    font-size: 1.2rem;
    padding: 1rem;
    border-radius: 10rem;
    outline: none;
  }
  
  .container button:hover {
    opacity: 0.9;
    cursor: pointer;
  }